a {
  text-decoration: none;
  color: inherit; }

.motherDivFooter {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: repeat(3, 1fr);
  height: 150px;
  width: 100%;
  position: sticky;
  background: #333;
  color: #e4e4e4; }
  .motherDivFooter .name {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    grid-column: 1;
    grid-row: 1/4;
    font-size: 1.2rem; }
    .motherDivFooter .name .nameDiv {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 45%;
      grid-row: 2; }
      @media (max-width: 600px) {
        .motherDivFooter .name .nameDiv {
          width: 70%; } }
      @media (max-width: 450px) {
        .motherDivFooter .name .nameDiv {
          width: 100%; } }
  .motherDivFooter .storyDiv {
    grid-column: 2;
    display: flex;
    align-items: center;
    justify-content: flex-end; }
    .motherDivFooter .storyDiv .story {
      display: flex;
      justify-content: center;
      align-items: center;
      height: 70%;
      width: 40%; }
      @media (max-width: 600px) {
        .motherDivFooter .storyDiv .story {
          width: 60%; } }
  .motherDivFooter .contactDiv {
    grid-column: 2;
    display: flex;
    align-items: center;
    justify-content: flex-end; }
    .motherDivFooter .contactDiv .contact {
      width: 40%;
      background: #FFE100;
      color: black;
      height: 70%;
      display: flex;
      justify-content: center;
      align-items: center;
      max-width: 500px;
      transition: all ease-in-out 0.25s; }
      .motherDivFooter .contactDiv .contact:hover {
        width: 65%;
        border-left: #00bcc5 10px solid;
        border-right: none;
        box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
        cursor: pointer; }
      @media (max-width: 600px) {
        .motherDivFooter .contactDiv .contact:hover {
          width: 100%; } }
    @media (max-width: 600px) {
      .motherDivFooter .contactDiv .contact {
        width: 60%; } }
  .motherDivFooter .workDiv {
    grid-column: 2;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-end; }
    .motherDivFooter .workDiv .work {
      width: 40%;
      height: 70%;
      display: flex;
      justify-content: center;
      align-items: center; }
      @media (max-width: 600px) {
        .motherDivFooter .workDiv .work {
          width: 60%; } }
